import util from '../../common/util.js';
export default {
	props: {
		news: {
			type: [Array]
		},
		columnId: {
			type: [Number, String]
		}
	},
	components: {
	},
	data() {
		return {
			newsList: [],
			requestParams: {
				columnId: 0,
				minId: 0,
				pageSize: 10,
				column: 'id,post_id,title,author_name,cover,published_at,comments_count'
			},
			getContent: {
				text: '加载中...',
				state: false
			},
			detailState: false
		}
	},
	created () {
		this.requestParams.columnId = this.columnId;
		this.getNews();
	},
	methods: {
		getNews () {
			let _this = this;
			uni.request({
				// url: this.$host + 'api/news',
				url: 'https://unidemo.dcloud.net.cn/api/news',
				data: _this.requestParams,
				success (res) {
					if (res.statusCode == 200) {
						let list = res.data; 
						list.map( (item) => {
							item.published_at = util.friendlyDate(new Date(item.published_at.replace(/\-/g, '/')).getTime());
							return _this.newsList.push(item);
						});
						_this.getContent.state = true;
					} else {
						_this.getContent.state = false;
					}
				}
			})
		},
		handleChufa (e) {
			this.getNews();
			console.log(e, '触发');
		},
		itemClick (val) {
			this.detailState = true;
			uni.request({
				url: 'https://unidemo.dcloud.net.cn/api/news' + '/36kr/' + val,
				success(res) {
					var obj = res;
					uni.navigateTo({
					    url: '/pages/detail/detail?obj=' + encodeURIComponent(JSON.stringify(obj))
					});
				}
			})
		},
		handleDelete (id) {
			console.log(1);
			let _this = this;
			uni.showModal({
			    title: '不感兴趣?',
				content: ' ',
			    success: function (res) {
			        if (res.confirm) {
						_this.newsList = _this.newsList.filter( (item) => {return item.post_id != id});
			            console.log('用户点击确定');
			        } else if (res.cancel) {
			            console.log('用户点击取消');
			        }
			    }
			});
		}
	}
}
